// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

body {
  font-family: 'Lato';
}

.fa-btn {
  margin-right: 6px;
}

div.center {
  width: 100%;
  text-align: center;
}

#action-bar {
  margin: 10px 0 10px 0;
  a {
    display: inline-block;
  }
}

#posts {
  th:nth-child(2), td:nth-child(2) {
    width: 175px;
  }
  th:nth-child(3), td:nth-child(3) {
    width: 125px;
    text-align: center;
  }
  th:nth-child(4), td:nth-child(4) {
    width: 75px;
    text-align: right;
    a {
      display: inline-block;
    }
  }

  .fa {
    margin-left: 10px;
  }
}

#users {
  th:nth-child(2), td:nth-child(2) {
    width: 100px;
    text-align: center;
  }
  th:nth-child(3), td:nth-child(3),
  th:nth-child(4), td:nth-child(4) {
    width: 160px;
  }
  th:nth-child(5), td:nth-child(5) {
    width: 75px;
    text-align: right;
    a {
      display: inline-block;
    }
  }

  .fa {
    margin-left: 10px;
  }
}

#alerts {
  margin-top: 10px;
}

#alerts .alert-info {
  display: none;
}

#post-marks {
  a {
    color: unset;
  }
  .fa-eraser {
    color: #e04251;
  }
}

#fbpages {
  td:first-child {
    font-weight: bold;
    padding-right: 10px;
  }

  i.fa-check {
    color: green;
  }

  i.fa-close {
    color: #e04251;
    cursor: pointer;
    top: 13px;
    right: 30px;
    position: absolute;
  }
}